*{
box-sizing: border-box
}

a{ 
    text-decoration: none;
}

.about {
    font-family: var(--font-family-roobert-medium), sans-serif;
    font-size: 4em;
    ;
}

.about:hover .char {
    display: inline-block;
    animation: jump .4s linear infinite alternate;
    animation-delay: calc( -.8s + ( var(--char-index)*.1s ) );
    animation-timing-function: ease-in-out;
}

.about:hover .char:nth-of-type(odd) {
    display: inline-block;
    animation: jump-reverse .4s linear infinite alternate;
    animation-delay: calc( -.8s + ( var(--char-index)*.1s ) );
    animation-timing-function: ease-in-out;
}

@keyframes jump { 
    0% {
      transform:translateY(0);
      
    }
    
    100% {
      transform: translate(0%, -8%) rotate(4deg);
    }
  }

@keyframes jump-reverse { 
  0% {
    transform:translateY(0);
  }
  
  100% {
    transform: translate(0%, 8%) rotate(-4deg);
  }
}


.blogh-title {
  font-family:  var(--font-family-blogh);
  color: var(--vermilion);
  font-size: 100px;
}

#nav-bar .blogh {
  font-family:  var(--font-family-blogh);
  color: var(--vermilion);
  font-size: 70px;
}


.blogh-small {
  font-family:  var(--font-family-blogh);
  color: var(--vermilion);
  font-size: 41px;
}
.blogh-xs {
  font-family:  var(--font-family-blogh);
