#vertical-nav {
position: fixed;
right: 0;
top: 0;
z-index: 10;
width: 75px;
height: 100vh;
border-left: 2px solid var(--vermilion);
background-color: var(--pale-blue);
}

.flex-col {
  min-height: 100%;
    width: 54px;
    flex-direction: column;
    align-items: center;
    align-content: space-around;
    justify-content: space-between;
    gap: 100px;
    margin: 68px auto;
    display: flex;
}

.pacman {
width: 3rem;
height: 3rem;
border-radius: 100%;
align-self: center;
animation: infinite pacman-walk var(--animation-duration) var(--animation-easing);
animation-play-state: paused;
rotate: 180deg
}

.pacman:hover {
content: "";
display: block;
background: var(--vermilion);
clip-path: polygon(50% 50%, 80% 0, 100% 0, 100% 100%, 0 100%, 0 0, 20% 0);
border-radius: 100%;
animation: infinite chomp-chomp-chomp 250ms linear paused
}

.pacman:after {
content: "";
display: block;
height: 100%;
width: 100%;
background: var(--vermilion);
clip-path: polygon(50% 50%, 80% 0, 100% 0, 100% 100%, 0 100%, 0 0, 20% 0);
border-radius: 100%;
animation: infinite chomp-chomp-chomp 250ms linear paused
}

@keyframes chomp-chomp-chomp {
0% {
    clip-path: polygon(50% 50%, 100% 0, 100% 0, 100% 100%, 0 100%, 0 0, 0 0)
}

50% {
    clip-path: polygon(50% 50%, 50% 0, 100% 0, 100% 100%, 0 100%, 0 0, 50% 0)
}
}

@keyframes shake-rectangle-02 {
0% {
    transform: translate(0, 14px) rotate(-7deg)
}

33% {
    transform: translate(-3px, 14px) rotate(-9deg)
}

66% {
    transform: translate(-5px, 16px) rotate(-7deg)
}

100% {
    transform: translate(-5px, 14px) rotate(-9deg)
}
}

.nav {
max-width: 40%;
display: flex;
vertical-align: middle;
justify-content: space-between;
gap: 20px
}

.hamburger {
align-items: flex-start;
display: flex;
flex-direction: column;
gap: 10px;
margin-left: 2px;
min-height: 53px;
width: 52px
}

.path {
height: 10px;
margin: 0 auto
}

.pacman-nav-group {
align-items: flex-start;
display: flex;
flex-direction: column;
margin-left: 1px;
min-height: 209px;
width: 9px
}

.oval {
background: 0 0;
color: var(--color-light);

}

.group-6 {
align-items: center;
display: flex;
flex-direction: column;
gap: 65px;
min-height: 327px;
padding: 0 1px;
width: 54px
}

.pacman-nav-group:focus,
.pacman-nav-group:hover {
outline: 0;
animation-play-state: running
}

.pacman-nav-group:focus .pacman,
.pacman-nav-group:focus .pacman:after,
.pacman-nav-group:hover .pacman-nav-group:hover .pacman,
.pacman-nav-group:hover .pacman:after {
animation-play-state: running
}

.oval:active {
color: var(--color-accent)
}

.oval {
background-color: var(--vermilion);
border-radius: 9px;
height: 9px;
margin-top: 58px;
width: 9px
}

