}

.thin-line-smaller-margin {
  height: 1px;
  width: 100%;
  background: var(--vermilion);
  margin: 50px 0 50px 0
  }
  

.external-link-arrow {
height: 25px;
margin-left: 5px
}

.external-link-arrow-smaller {
  height: 22px;
  margin-left: 5px
  }

.external-link {
